@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

hr {
  margin: 15px;
  background: -webkit-gradient(linear, left top, right top, from(white), color-stop(rgb(136, 136, 136)), to(white));
  background: linear-gradient(to right, white, rgb(136, 136, 136), white);
  height: 2px;
  border: none;
  color: red;
}

.Parallax{

  background-image: url(/Portfolio/static/media/laptop-screen.0279db96.jpg);
  height: 1000px;

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.TitleContainer{
  text-align: center;
}

.Parallax-text {
    position: absolute;
    width: 75%;
    background-color: rgb(216, 213, 56);
    top: 55%;
    left: 50%;
    color: rgb(255, 255, 255);
    -webkit-text-stroke: 2px rgb(0, 0, 0);
    font-size: 40px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: 'Lato', sans-serif;
}

@media only screen and (max-device-width: 900px) {
  .Parallax {

    height: 450px;

  }
  .Parallax-text {
    position: absolute;
    width: 80%;
    top: 30%;
    left: 50%;
    color: rgb(255, 255, 255);
    -webkit-text-stroke: 1px rgb(0, 0, 0);
    font-size: 15px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
}

