body {
  margin: 0;
}

.slider {
position: relative;
height: 200px;
overflow: hidden;
text-align: center;
}

.slide {
position: absolute;
display: flex;
justify-content: center;
align-items: center;
width: 100%;
height: 100%;
opacity: 0;

}

.slide_active {
  opacity: 1; 
  -webkit-transition: all 1.1s ease-in; 
  transition: all 1.1s ease-in;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.image{ 
  position: absolute;
  object-fit: cover;
  
}

.formDiv {
    width: 90%;
    position: relative;
}

.nameInput {
    width: 100%;
    margin: 10px 0 5px 0;
    height: 40px;
    font-size: 16px;
    padding: 0 0 0 5px;
    outline: none;
}

.emailInput {
    width: 100%;
    margin: 10px 0 5px 0;
    height: 40px;
    font-size: 16px;
    padding: 0 0 0 5px;
    outline: none;
}


.msgInput {
    width: 100%;
    height: 130px;
    margin: 10px 0 5px 0;
    padding: 5px 0 0 5px;
    font-size: 17px;
    resize: none;
    outline: none;
}

.btnSend {
    background-color: transparent;
    color: rgb(216, 213, 56);
    letter-spacing: .13em;
    outline: none;
    height: 40px;
    width: 100%;
    border: 2px solid rgb(216, 213, 56);
}

.btnSend:hover {
    background-color: rgb(216, 213, 56);
    color: white;
    outline: none;
    letter-spacing: .13em;
    border: 2px solid rgb(216, 213, 56);
    -webkit-transition: all .7s ease-in-out;
    transition: all .7s ease-in-out;
}

.msgDiv, .emailDiv {
    text-align: center;
    position: relative;
    overflow-y: hidden;
}

#float_name_lbl {
    
    background-color: rgb(216, 213, 56);
    position: absolute;
    top: 54px;
    width: 100%;
    height: 0px;
    overflow: hidden;
    -webkit-transition: ease-in-out 0.5s;
    transition: ease-in-out 0.5s;
 }

#float_email_lbl {
    visibility: hidden;
    background-color: rgb(216, 213, 56);
    position: absolute;
    top: 54px;
    width: 100%;
    height: 0px;
    overflow: hidden;
    -webkit-transition: ease-in-out 0.5s;
    transition: ease-in-out 0.5s;
 }
 
#float_msg_lbl {
   visibility: hidden;
   position: absolute;   
   background-color: rgb(216, 213, 56);
   width: 100%;
   top: 147px;
   height: 0px;
   overflow-y: hidden;
   -webkit-transition: ease-in-out 0.5s;
   transition: ease-in-out 0.5s;
}

#enterEmail, #enterName {
    color: white;
    font-size: 15px;
    letter-spacing: .10em;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

#enterMsg {  
    color: white;
    letter-spacing: .10em;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}



@media only screen and (min-device-width: 900px) {
    
    .msgInput:focus + #float_msg_lbl {
        visibility: visible;
        top: 127px;
        height:20px;
        -webkit-transition: ease-in-out 0.5s;
        transition: ease-in-out 0.5s;       
    }

    .emailInput:focus + #float_email_lbl {
        visibility: visible;
        top: 39px;
        height: 15px;
        -webkit-transition: ease-in-out 0.5s;
        transition: ease-in-out 0.5s;
    }

    .nameInput:focus + #float_name_lbl {
        visibility: visible;
        top: 39px;
        height: 15px;
        -webkit-transition: ease-in-out 0.5s;
        transition: ease-in-out 0.5s;
    }

}

.contactWrapper {
    background-color: rgb(36, 34, 34);
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1.7fr 1fr;
}

.contactContainer {
    height: 150px;
    grid-column: 2 / 3;
}


.btnContact {
    background-color: transparent;
    border: 2px solid rgb(216, 213, 56);
    color: rgb(216, 213, 56);
    padding: 21px 34px;
    outline: none;
    letter-spacing: .13em;
    margin: 20px 0 0 0;
    -webkit-transition: 1s;
    transition: 1s;
}

.btnContactOff {
    background-color: rgb(216, 213, 56);
    color: white;
    padding: 21px 34px;
    outline: none;
    letter-spacing: .13em;
    margin: 20px 0 0 0;
    border: 2px solid rgb(216, 213, 56);
    -webkit-transition: 1s;
    transition: 1s;
}


.hide_Container {
    height: 0;
    width: 100%;
    margin: 0 0 20px 0;
    grid-column: 2 / 3;
    border: .5px solid rgb(216, 213, 56); 
    -webkit-transition: 1s; 
    transition: 1s;
    -webkit-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
            transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.show_Container {
    position: absolute;
    -webkit-transition: 2s;
    transition: 2s;
    -webkit-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
            transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
    height: 350px;
    border: 2px solid rgb(216, 213, 56); 
    width: 100%;
    overflow-y: hidden;
    grid-column: 2 / 3;
    position: relative;
    margin: 0 0 20px 0;
}


.infoBox{
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    padding: 5px;
    align-items: center;
}

@media only screen and (max-device-width: 900px) {
